import Navbar from "./components/NavBar";
import { useState, useReducer, createContext } from "react";
import { SearchContext } from "./components/NavBar";
import PageRoute from "./components/DataTable/PageRoute";
import { ThemeProvider } from "@material-ui/styles";
import { createTheme, CssBaseline } from "@material-ui/core";
import { useListPostsQuery } from "./components/data";
import CircularProgress from "@mui/material/CircularProgress";

export const ThemeContext = createContext();

const initialState = {
  darkMode: false,
};

const themeReducer = (state, action) => {
  switch (action.type) {
    case "LIGHTMODE":
      return { darkMode: false };
    case "DARKMODE":
      return { darkMode: true };
    default:
      return state;
  }
};

function App() {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(20);
  const { data: coins, isLoading } = useListPostsQuery({
    page: page + 1,
    limit: rowsPerPage,
  });

  const { data: allCoins } = useListPostsQuery({
    page: page + 1,
    limit: 100,
  });

  const [state, dispatch] = useReducer(themeReducer, initialState);
  const [search, setSearch] = useState("");

  const darkTheme = createTheme({
    palette: {
      type: state.darkMode ? "dark" : "light",
    },
  });

  return (
    <ThemeContext.Provider value={{ state, dispatch }}>
      {!isLoading && (
        <ThemeProvider theme={darkTheme}>
          <CssBaseline />
          <SearchContext.Provider value={setSearch}>
            <Navbar search={search} changePage={setPage} />
          </SearchContext.Provider>
          <PageRoute
            onChangeRowsPerPage={setRowsPerPage}
            rowsPerPage={rowsPerPage}
            coins={search === "" ? coins : allCoins}
            search={search}
            changePage={setPage}
            page={page}
          />
        </ThemeProvider>
      )}
      {isLoading && (
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            fontSize: "30px",
          }}
        >
          <CircularProgress disableShrink />
          <p style={{ paddingLeft: 20 }}>Loading...</p>
        </div>
      )}
    </ThemeContext.Provider>
  );
}

export default App;
